let bankArr=['中国银行','农业银行','建设银行','农村信用社'];
export default {
	data() {
		return {
			bankArr:bankArr,
			bank:0,
			cardname:"",
			card:"",
			date_time:"",
			balance:"",
			total:"",
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		// 单列选择
		changeOne(val){
			let arr=[];
			switch (val){
				case 'bank':
				arr=bankArr;
					break;
			}
			uni.showActionSheet({
				itemList: arr,
				success: res => {
					switch (val){
						case 'bank':
						this.bank=res.tapIndex;
							break;
					}
				},
			});
		},
		//添加信用卡
		async submit(){
		   uni.showLoading({ title: '发布中...', mask: true });
		   try{
		   	let [err,res] = await this.$http.post('/creditcard/add',{
		   		card:this.card,
		   		cardname:this.cardname,
		   		bank:this.bank,
				total:this.total,
				balance:this.balance,
				date_time:this.date_time
		   	},{
		   		token:true,
		   		checkToken:true,
		   		checkAuth:true
		   	});
			if(!this.card){
				return uni.showToast({ title: '请输入信用卡卡号', icon:"none" });
			}
			if(!this.cardname){
				return uni.showToast({ title: '姓名不能为空', icon:"none" });
			}
		   	// 发布失败
		   	if (!this.$http.errorCheck(err,res)) {
		   		return uni.hideLoading();
		   	}
		   	// 发布成功
		   	uni.hideLoading();
		   	uni.showToast({
		   		title: '发布成功！'
		   	});
		   	uni.$emit('updateData',{ 
		   		type:"updateList",
		   		data:res.data.data.detail
		   	});
		   	uni.navigateBack({ delta: 1 });
		   }catch(e){
		   	return;
		   }	
		},
		// 修改时间
		bindDateChange(e) {
			this.date_time = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
}
